@import "mixins/mixins";
/* ==========================================================================
 Fonts
 *========================================================================== */
@include font("OpenSans", OpenSans/opensans-condlight/opensans-condlight, 100, normal);
@include font("OpenSans", OpenSans/opensans-condbold/opensans-condbold, 200, normal);
@include font("OpenSans", OpenSans/opensans-light/opensans-light, 300, normal);
@include font("OpenSans", OpenSans/opensans-regular/opensans-regular, 400, normal);
@include font("OpenSans", OpenSans/opensans-italic/opensans-italic, 400, italic);
@include font("OpenSans", OpenSans/opensans-bold/opensans-bold, 700, normal);
@include font("OpenSans", OpenSans/opensans-extrabold/opensans-extrabold, 900, normal);
